<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示oss云存储配置列表信息</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button v-prem="'/ossCloudStorage/save'" icon="add" type="success" size="small" @click="$routerLink('/ossCloudStorage/save','save')">新增</cms-button>
      <cms-button v-prem="'/ossCloudStorage/delete'" icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="ossName" label="名称" min-width='160' ></el-table-column>
      <el-table-column prop="accessDomain" label="访问域名" min-width='160'></el-table-column>
      <el-table-column prop="appId" label="APPID" min-width="160"></el-table-column>
       <el-table-column prop="appKey" label="appKey" min-width="160"></el-table-column>
      <el-table-column prop="bucketArea" label="地区码" min-width="160"></el-table-column>
      <el-table-column prop="endPoint" label="end_point" min-width="140"></el-table-column>
      <el-table-column prop="bucketName" label="bucket名" min-width="140"></el-table-column>
      <el-table-column prop="ossType" label="存储类型" min-width="110">
        <div slot-scope="scope">
          <span v-if="scope.row.ossType===1">腾讯云cos</span>
          <span v-if="scope.row.ossType===2">阿里云oss</span>
          <span v-if="scope.row.ossType===3">七牛云</span>
        </div>
      </el-table-column>
      <el-table-column prop="secretId" label="secret_id" min-width="110"></el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button v-prem="'/ossCloudStorage/update'" icon='edit' size='small' type='info' @click.stop="$routerLink('/ossCloudStorage/update','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button v-prem="'/ossCloudStorage/delete'" icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  occPage,
  occDelete
} from '@/api/setting/occCloudStorage.js'
var defaultData = {
  size: 10,
  page: 1,
  name: ''
}
export default {
  name: 'ossCloudStorageList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(occDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(occDelete, this.ids) // 这个才是真实的删除方法
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(occPage, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(occPage, this.queryParams)
  }
}
</script>

<style>
</style>



